import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import './governance.less'
export default function Governance({ lang }) {
  const [curTab, setCurTab] = useState('finished')
  const switchTab = async (current) => {
    setCurTab(current)
  }
  return (
    <div className='governance-box'>
      <div className='check'>
        <div className={curTab === 'active' ? 'active now' : 'active'} onClick={() => switchTab('active')}>{lang['vote-now']}</div>
        <div className={curTab === 'finished' ? 'finished now' : 'finished'} onClick={() => switchTab('finished')}>
          {lang['closed']}
        </div>
      </div>
      {curTab === 'active' && <div className='now-prom'>
        {lang['no-proposals-found']}
      </div>}
      {curTab === 'finished' && <div className='finished-box'>
        <div className='finished-title'>
          <div className='title-text'>
            {lang['closed-title']}
          </div>
          <div className='title-describe'>
            {lang['closed-title-describe']}
          </div>
        </div>
        <div className='finished-detail'>
          <Link to='/diptwo'>
            <div className='finished-detail-box'>
              <div className='finished-detail-box-title'>
                {lang['dip-two']}
              </div>
              <div className='finished-detail-box-describe'>
                <span>
                  {lang['dip-two-describe']}
                </span>
              </div>
            </div>
          </Link>
          <Link to='/dipone'>
            <div className='finished-detail-box dip-one'>
              <div className='finished-detail-box-title'>
                {lang['dip-one']}
              </div>
              <div className='finished-detail-box-describe'>
                <span>
                  {lang['dip-one-describe']}
                </span>
              </div>
            </div>
          </Link>

        </div>
      </div>}
    </div>
  )
}